
var Converter = require('csvtojson').Converter;
var fs = require('fs');
var DateTimeMethod = require('../appliation/dateTimevalidation');
var numberValidationMethod = require('../appliation/txnIdValidation');
var amountValidationMethod = require('../appliation/txnAmountvalidation');

var BusinessRuleMethod = {

applybusinessRules:function(jsonObj)
{
	console.log("entered");

	var validationResults = [];

	for (var obj in jsonObj) {

		console.log("object going to switch loop - "+ obj);


	        switch (obj) {
				case 'txid':

				console.log("Enetred txid case ");


					var numberValidation = numberValidationMethod.numberValidation(jsonObj[obj]);

					console.log("numberValidation after validation "+numberValidation);




			        if (!numberValidation)
			        	{

			        		console.log("Number Validation -" +numberValidationMethod.numberValidation(jsonObj[obj]));



var text = '{[' +
'{ "txid":'+obj+' , "description":"txid is not in number format" }]}';


			
var jsonresultobj = JSON.parse(text);

					console.log("number obj -"+jsonresultobj);

					validationResults.push(jsonresultobj);

			    		}
			        break;
				case 'clientid':
			        if (jsonObj[obj]=='') 
			        	{
			        				        		var obj = {
					    'txid': jsonObj[obj],
					    'description': 'client id cannot be null or empty'
					   
					}
					validationResults.push(obj);

			    		}
			        break;
				case 'txnamount': // assuming amount is number format checking if valus is Not a number
			        if (!amountValidationMethod.amountValidation(jsonObj[obj])) 
			        	{		        	
			        		
	var obj = {
					    'txid': jsonObj[obj],
					    'description': 'txnamount is not in expected format'
					   
					}
					validationResults.push(obj);


			    		}
			        break;
				case 'product':
			       

			        break;
				case 'datetime':

					if(!DateTimeMethod.dateValidation(jsonObj[obj]))
					{
						var obj = {
					    'txid': jsonObj[obj],
					    'description': 'Date time provided is either in future or not valid'
					   
					}
					validationResults.push(obj);

					}
			        break;					
			    default:
			    	
						var obj = {
					    'txid': jsonObj[obj],
					    'description': 'unexpected key and this key is not handled',
					   
					}
					validationResults.push(obj);
			        break;
	        };


	}

return validationResults;

}

}

module.exports = BusinessRuleMethod;